.superInput {
    border: 2px solid #aaa;
    border-radius: 5px;
    margin: 8px 0;
    outline: none;
    padding: 8px;
    box-sizing: border-box;
    transition: .3s;
}
.superInput:focus{
    border-color: dodgerBlue;
    box-shadow: 0 0 8px 0 dodgerBlue;
}

.errorInput {
    border: 2px solid #ED5464FF;
    border-radius: 5px;
}
.errorInput:focus{
    box-shadow: 0 0 8px 0 #ED5464FF;
}

.error {
    display: block;
    color: red;
    width: 170px;
    height: 20px;
    text-align: center;
}