.select {
    width: 150px;
    padding: 5px;
    border-radius: 10px;
    cursor: pointer;
    padding-right: 50px;
    appearance: none;
    background: url('https://www.shareicon.net/download/2015/11/10/669927_arrows_512x512.png');
    background-color: white;
    background-repeat: no-repeat, repeat;
    background-position: right .7em top 50%, 0 0;
    background-size: .65em auto, 100%;
    border: 2px inset blue;
    color: mediumvioletred;

}

.select.focus {
    outline: none;
    border: darkcyan solid 1px;
}


.option {
    background: forestgreen;

}

.option:checked {
    background: forestgreen;

}

.option:hover {
    box-shadow: 0 0 10px 100px white inset;
    transition: all .2s ease-in-out;
    background: fuchsia;
}